import { FETCH_COUNTRIES, SEARCH_COUNTRIES, SORT } from "../actions";
import { ALFA_ASC } from "../../constants/sort";
const initialState = {
  countries: [],
  filteredCountries: [],
};

export default function reducer(state = initialState, action) {
  switch (action.type) {
    case FETCH_COUNTRIES:
      return {
        ...state,
        countries: action.payload,
        filteredCountries: action.payload,
      };
    case SEARCH_COUNTRIES:
      return {
        ...state,
        filteredCountries: action.payload,
      };
    case SORT:
      let orderCountries = [...state.countries];
      orderCountries = orderCountries.sort((a,b) => {
        if (a.name < b.name) {
          return action.payload === ALFA_ASC ? -1 : 1;
        }
        if (a.name > b.name) {
          return action.payload === ALFA_ASC ? 1 : -1;
        }
        return 0;
      });
      return {
        ...state,
        filteredCountries: orderCountries,
      };
    default:
      return state;
  }
}
