import { useState, useHistory } from "react"
import axios from "axios"
export default function AddActivity (){
   const [actividad, setActividad] =useState({})
   let history= useHistory() 
   function onInputChange (e){
       e.preventDefault()
       setActividad({
           ...actividad,
           [e.target.name]: e.target.value
       })
    }
   function onSubmit(e){
       e.preventDefault()
   axios.post('http://localhost:3001/activity', actividad)
   .then(()=>{
       history.push('/')
   })
   }
   return <form onSubmit={onSubmit}>
        <label htmlFor="">Nombre:</label>
        <input onChange={onInputChange} name='name'type="text" value={actividad.name}/>
        <label htmlFor="">Dificultad:</label>
        <input onChange={onInputChange} name='dificultad'type="text" value = {actividad.dificulty}/>
        <label htmlFor="">Duración:</label>
        <input name='duracion'type="text" value={actividad.duration}/>
        <label htmlFor="">Estación:</label>
        <input name='estacion'type="text" value={actividad.season}/>
        <input type="submit" />
    </form>
}